.icons {
   stroke   : #e1c2b6;
   /* for svg icons*/
   width    : 2rem;
   /* for svg icons*/
   fill     : #e1c2b6;
   /* for svg icons*/
   filter   : drop-shadow(0px 6px 4px rgba(0, 0, 0, 0.25));
   opacity  : 1;
   transform: rotate(0deg);
}

.icons:hover {
   opacity     : 0.8;
   stroke      : white;
   fill        : white;
   /* transform: rotate(-45deg);   */
   transition  : 0.2s ease-in-out;
}