.contact_left {
  /* border             : 1px red dashed; */
  flex               : auto;
  position           : relative;
  width              : 40%;
  padding-right      : 20px;
  left               : -1000px;
  visibility         : hidden;
  animation          : left-fly-in 1s ease-in-out;
  animation-delay    : 0.1s;
  animation-fill-mode: forwards;
}

@keyframes left-fly-in {
  100% {
    visibility: visible;
    left      : 0px;
  }
}

.contact_left h1 {
  font-size : calc(1.1rem + 3vw);
  margin    : 1rem 0 1rem;
  text-align: left;
  color     : #ffffff;
}

.contact_left i {
  font-size: calc(.8rem + 2vw);
}

.contact_left p {
  margin: 30px 0 10px;
}

.contact_left input {
  font-size       : 1.5rem;
  width           : calc(90%);
  text-align      : left;
  color           : #564141;
  border-radius   : 10px;
  margin          : 5px;
  padding         : 5px;
  background-color: antiquewhite;
}

.contact_left input:focus {
  background-color: white;
}

.contact_left input::placeholder {
  color: lightgrey;
}

.contact_right {
  /* border                      : 1px green dashed; */
  flex               : auto;
  position           : relative;
  width              : 55%;
  left               : 1000px;
  visibility         : hidden;
  animation          : right-fly-in 1s ease-in-out;
  animation-delay    : 0.1s;
  animation-fill-mode: forwards;
}

@keyframes right-fly-in {
  100% {
    visibility: visible;
    left      : 0px;
  }
}

.contact_right_pic {
  border-radius: 15px;
  min-width    : 100px;
  max-height   : 100%;
  max-width    : 100%;
  height       : 50vh;
  object-fit   : cover;
}

.contact_right_info {
  font-size: calc(0.8rem + 1vw);
}

.contact_right p {
  margin   : 0;
  padding  : 10px 20px 0;
  color    : #564141;
  min-width: 30vw;
}

.contact_right .address {
  display    : flex;
  margin-left: 1rem;
  align-items: center;
}

.contact_right .address>button>a>img {
  height: calc(1rem + 2vw);
}